<template>
  <div class="login-container">
    <div class="loginBox">
      <div class="loginBoxLeft">
        <img src="../../../static/images/loginImg2.png" alt />
      </div>
      <div class="loginBoxRight">
        <el-form ref="loginForm" :model="loginForm" :rules="loginRules" class="login-form" label-position="left">
          <div class="title-container">
            <h3 class="title">登录</h3>
          </div>
          <el-form-item prop="username" :class="{'focusBorder':(focusVal==1)}">
            <!-- <span class="svg-container">
          <svg-icon icon-class="user" />
            </span>-->
            <div class="labelText">账号</div>
            <el-popover v-model="showReBool" placement="bottom-start" width="330" popper-class="re-pop" :disabled="reAccPass.length==0">
              <el-input ref="username" v-model="loginForm.username" type="text" tabindex="1" @focus="focusVal=1" @blur="focusVal=null" @input="fillPass" slot="reference" />
              <!-- 记住的帐号 -->
              <ul class="re-admin">
                <li @click="reAssig(item)" v-for="(item,index) in reAccPass" :key="index">
                  <span class="re-name">{{item.username}}</span>
                  <span class="re-del el-icon-delete" @click.stop="reDelF(index)"></span>
                </li>
              </ul>
            </el-popover>
          </el-form-item>
          <el-input type="text" name="username" autofocus class="donShow"></el-input>
          <el-input type="password" name="password" autofocus class="donShow"></el-input>
          <el-tooltip v-model="capsTooltip" content="Caps lock is On" placement="right" manual>
            <el-form-item prop="password" :class="{'focusBorder':(focusVal==2)}">
              <!-- <span class="svg-container">
            <svg-icon icon-class="password" />
              </span>-->
              <div class="labelText">密码</div>
              <el-input tabindex="2" :key="passwordType" ref="password" v-model="loginForm.password" :type="passwordType" autocomplete="off" @keyup.native="checkCapslock" @blur="capsTooltip = false;focusVal=null" @keyup.enter.native="handleLogin" @focus="focusVal=2" />
              <span class="show-pwd" @click="showPwd">
                <svg-icon :icon-class="passwordType === 'password' ? 'eye' : 'eye-open'" />
              </span>
            </el-form-item>
          </el-tooltip>
          <div class="rememberPass">
            <el-checkbox v-model="rePass">记住密码</el-checkbox>
          </div>
          <div class="bottomBox">
            <el-button :loading="loading" type="primary" style="width:100%;margin-bottom:30px;" @click.native.prevent="handleLogin">登录</el-button>
          </div>
        </el-form>
      </div>
    </div>
    <div class="ver">版权所有：深圳北斗通信科技有限公司 &nbsp&nbsp&nbsp&nbspV4.1.3</div>
  </div>
</template>

<script>
// import { validUsername } from "@/utils/validate";
//  选择语言组件
// import LangSelect from '@/components/LangSelect'
//  第三方登录组件
// import SocialSign from './components/SocialSignin'
import axios from "axios";
import qs from "qs";
import { login, register } from "../../api/user";
export default {
  name: "Login",
  //  选择语言,第三方登录
  // components: { LangSelect, SocialSign },
  data() {
    const validateUsername = (rule, value, callback) => {
      if (value == "") {
        callback(new Error("请输入用户名"));
      } else {
        callback();
      }
    };
    const validatePassword = (rule, value, callback) => {
      if (value.length < 2) {
        callback(new Error("密码不能少于2位"));
      } else {
        callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.registerForm.password !== "") {
          this.$refs.registerForm.validateField("password");
        }
        callback();
      }
    };
    var validateName = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入姓名"));
      } else {
        if (this.IsChinese(value)) {
          callback(new Error("必须是中文"));
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.registerForm.pass) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    var validateUserCode = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入身份证号"));
      } else if (
        !/^\d{6}(18|19|20)?\d{2}(0[1-9]|1[012])(0[1-9]|[12]\d|3[01])\d{3}(\d|X)$/i.test(
          value
        )
      ) {
        callback(new Error("身份证号格式错误!"));
      } else {
        callback();
      }
    };

    return {
      showReBool: false,// 显示记住帐号弹窗
      reAccPass: [],// 所有的帐号密码
      rePass: false,// 记住密码
      loginForm: {
        username: "",
        password: ""
      },
      loginRules: {
        username: [
          {
            required: true,
            trigger: "blur",
            validator: validateUsername
          }
        ],
        password: [
          {
            required: true,
            trigger: "blur",
            validator: validatePassword
          }
        ]
      },
      registerForm: {
        username: "",
        name: "",
        userCode: "",
        pass: "",
        password: "",
        power: 2
      },
      registerRules: {
        username: [
          {
            required: true,
            trigger: "blur"
          }
        ],
        name: [
          {
            required: true,
            trigger: "blur",
            validator: validateName
          }
        ],
        userCode: [
          {
            required: true,
            trigger: "blur",
            validator: validateUserCode
          }
        ],
        pass: [{ validator: validatePass, trigger: "blur", required: true }],
        password: [
          { validator: validatePass2, trigger: "blur", required: true }
        ]
      },
      passwordType: "password",
      capsTooltip: false,
      loading: false,
      // showDialog: false,
      redirect: undefined,
      dialogVisibleRegister: false,
      focusVal: null
    };
  },
  watch: {
    $route: {
      handler: function (route) {
        this.redirect = route.query && route.query.redirect;
      },
      immediate: true
    }
  },
  created() {

    // window.addEventListener('storage', this.afterQRScan)
    sessionStorage.clear();

    if (!localStorage.rePassWord) {// 存储密码的数组；如果没有存储数组，则需要先创建
      localStorage.rePassWord = JSON.stringify([]);
      this.loginForm.username = '';
      this.loginForm.password = '';
      this.rePass = false;
    } else {// 有记录的密码则将第一项帐号密码填入
      this.reAccPass = JSON.parse(localStorage.rePassWord);
      if (this.reAccPass.length > 0) {
        this.loginForm.username = this.reAccPass[0].username;
        this.loginForm.password = atob(this.reAccPass[0].password);
        this.rePass = true;
      };
    };
  },
  mounted() {
    // if (this.loginForm.username === "") {
    //   this.$refs.username.focus();
    // } else if (this.loginForm.password === "") {
    //   this.$refs.password.focus();
    // }
  },
  destroyed() {
    // window.removeEventListener('storage', this.afterQRScan)
  },
  methods: {
    checkCapslock({ shiftKey, key } = {}) {
      if (key && key.length === 1) {
        if (
          (shiftKey && key >= "a" && key <= "z") ||
          (!shiftKey && key >= "A" && key <= "Z")
        ) {
          this.capsTooltip = true;
        } else {
          this.capsTooltip = false;
        }
      }
      if (key === "CapsLock" && this.capsTooltip === true) {
        this.capsTooltip = false;
      }
    },
    showPwd() {
      if (this.passwordType === "password") {
        this.passwordType = "";
      } else {
        this.passwordType = "password";
      }
      this.$nextTick(() => {
        this.$refs.password.focus();
      });
    },
    handleLogin() {
      var that = this;
      this.loading = true;
      setTimeout(() => {
        this.$store
          .dispatch("user/login", this.loginForm)
          .then((res) => {
            // 登录成功后是否保存密码
            that.reAccPass = JSON.parse(localStorage.rePassWord);// copy密码对象
            // 判断是否已经存在该帐号密码，存在则先从数组中删除这个对象(是为了确保最近的登录帐密为第一项)
            if (that.reAccPass.length > 0) {
              that.reAccPass.forEach((x, i) => {
                if (x.username == that.loginForm.username) {
                  that.reAccPass.splice(i, 1);
                };
              });
            };
            if (that.rePass) {// 记住密码
              that.reAccPass.unshift({ username: that.loginForm.username, password: btoa(that.loginForm.password) });
            };
            localStorage.rePassWord = JSON.stringify(that.reAccPass);// 更新本地
            //登录成功之后根据权限不同重定向到不同页面
            let power = res.Power;
              switch (power) {
                case 7:
                  this.$router.push('/carInOutProcedure');
                  break;
                case 6:
                  this.$router.push('/gunInOutProcedure');
                  break;
                case 4:
                  this.$router.push('/task');
                  break;
                  default:
                    this.$router.push('/')
              }
          })
          .catch(err => {
            this.$message.error(err); //登录失败提示错误
          })
          .finally(() => {
            this.loading = false;
          });
      }, 500);
    },
    IsChinese(val) {
      var str = this.trim(val);

      if (str.length != 0) {
        var reg = /^[\u4E00-\u9FA5]+$/;
        if (!reg.test(str)) {
          return true;
        }
      }
    },
    trim(str) {
      //删除左右两端的空格
      return str.toString().replace(/(^\s*)|(\s*$)/g, "");
    },
    submitRegisterForm(formName) {
      this.$refs[formName].validate(valid => {
        var registerForm1 = {
          username: this.registerForm.username,
          password: this.registerForm.password,
          name: this.registerForm.name,
          userCode: this.registerForm.userCode,
          power: 2
        };
        if (valid) {
          register(registerForm1)
            .then(response => {
              this.$message({
                message: "注册成功",
                type: "success"
              });
            })
            .catch(error => { });
        } else {
          return false;
        }
      });
    }

    // afterQRScan() {
    //   if (e.key === 'x-admin-oauth-code') {
    //     const code = getQueryObject(e.newValue)
    //     const codeMap = {
    //       wechat: 'code',
    //       tencent: 'code'
    //     }
    //     const type = codeMap[this.auth_type]
    //     const codeName = code[type]
    //     if (codeName) {
    //       this.$store.dispatch('LoginByThirdparty', codeName).then(() => {
    //         this.$router.push({ path: this.redirect || '/' })
    //       })
    //     } else {
    //       alert('第三方登录失败')
    //     }
    //   }
    // }
    , fillPass(e) {// 填充密码
      let _t = this;
      if (_t.reAccPass.length > 0) {
        // 查找是否存在帐号，有就填入,并且勾选上记住密码
        for (let x of _t.reAccPass) {
          if (x.username == e) {
            _t.loginForm.password = atob(x.password);
            _t.rePass = true;
            break;
          } else {// 没有就清空并取消勾选
            _t.loginForm.password = '';
            _t.rePass = false;
          };
        };
      };
      // 输入的时候隐藏记住帐号弹窗
      _t.showReBool = false;
    },
    reDelF(index) {// 删除帐号
      let _t = this;
      _t.reAccPass.splice(index, 1);
      localStorage.rePassWord = JSON.stringify(_t.reAccPass);// 更新本地
    },
    reAssig(item) {// 赋值
      let _t = this;
      _t.loginForm.username = item.username;
      _t.loginForm.password = atob(item.password);
      _t.rePass = true;// 勾选记住密码
      _t.$refs.loginForm.clearValidate('username');// 移除用户名验证
      _t.showReBool = false;// 隐藏记住帐号弹窗
    }
  }
};
</script>

<style lang="scss">
/* 修复input 背景不协调 和光标变色 */
/* Detail see https://github.com/PanJiaChen/vue-element-admin/pull/927 */

$bg: white;
$light_gray: #fff;
$blurColor: #31478f;
$cursor: #333;

@supports (-webkit-mask: none) and (not (cater-color: $cursor)) {
  .login-container .el-input input {
    color: $cursor;
  }
}

/* reset element-ui css */
.login-container {
  width: 100vw;
  height: 100vh;
  background: url("../../../static/images/loginImg1.png");
  background-size: cover;
  .el-input {
    display: inline-block;
    height: 47px;
    width: 85%;

    input {
      background: transparent;
      border: 0px;
      -webkit-appearance: none;
      border-radius: 0px;
      padding: 12px 5px 12px 15px;
      color: $light_gray;
      height: 47px;
      caret-color: $cursor;

      &:-webkit-autofill {
        box-shadow: 0 0 0px 1000px $bg inset !important;
        -webkit-text-fill-color: $cursor !important;
      }
    }
  }

  .el-form-item {
    border-bottom: 1px solid #999999;
    // background: rgba(0, 0, 0, 0.1);
    // border-radius: 5px;
    // color: #454545;
    margin-bottom: 26px;
    .el-input input {
      padding-left: 0;
      font-size: 16px;
      font-weight: 400;
      color: #333 !important;
      caret-color: #333;
    }
    &.focusBorder {
      border-bottom: 1px solid $blurColor;
      position: relative;
      &::after {
        // 两个像素的边框会闪动
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        background-color: $blurColor;
      }
    }
  }
}
// 记住密码
.donShow {
  position: fixed;
  left: -99999px;
  bottom: -9999999px;
  z-index: -999999;
  opacity: 0;
  input {
    color: #333 !important;
  }
}
.re-pop {
  padding: 8px;
  .re-admin {
    padding: 0;
    margin: 0;
    list-style: none;
    max-height: 200px;
    overflow-y: auto;
    &::-webkit-scrollbar {
      /*滚动条整体样式*/
      width: 4px; /*高宽分别对应横竖滚动条的尺寸*/
      height: 4px;
    }
    &::-webkit-scrollbar-thumb {
      /*滚动条里面小方块*/
      border-radius: 5px;
      -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
      background: rgba(0, 0, 0, 0.2);
    }
    li {
      // 每个选项的样式
      height: 40px;
      line-height: 40px;
      padding: 0 20px;
      position: relative;
      &:hover {
        background-color: #c0dcfc;
      }
      .re-del {
        // 删除
        position: absolute;
        top: 14px;
        right: 20px;
        z-index: 9;
        cursor: pointer;
      }
    }
  }
}
</style>

<style lang="scss" scoped>
$bg: white;
$dark_gray: #889aa4;
$light_gray: #eee;
$blurColor: #31478f;

.login-container {
  min-height: 100%;
  width: 100%;
  background-color: $bg;
  overflow: hidden;

  .login-form {
    position: relative;
    // width: 520px;
    max-width: 100%;
    padding: 81px 115px 0;
    margin: 0 auto;
    overflow: hidden;
  }

  .tips {
    font-size: 14px;
    color: #fff;
    margin-bottom: 10px;

    span {
      &:first-of-type {
        margin-right: 16px;
      }
    }
  }

  .svg-container {
    padding: 6px 5px 6px 15px;
    color: $dark_gray;
    vertical-align: middle;
    width: 30px;
    display: inline-block;
  }

  .title-container {
    position: relative;

    .title {
      font-size: 36px;
      color: $blurColor;
      margin: 0px auto 68px auto;
      text-align: center;
      font-weight: bold;
    }

    .set-language {
      color: #fff;
      position: absolute;
      top: 3px;
      font-size: 18px;
      right: 0px;
      cursor: pointer;
    }
  }

  .show-pwd {
    position: absolute;
    right: 10px;
    top: 20px;
    font-size: 16px;
    color: $dark_gray;
    cursor: pointer;
    user-select: none;
  }

  .thirdparty-button {
    position: absolute;
    right: 0;
    bottom: 6px;
  }

  @media only screen and (max-width: 470px) {
    .thirdparty-button {
      display: none;
    }
  }
  .bottomBox {
    position: relative;
    .el-button {
      background-color: $blurColor;
      font-size: 18px;
      font-weight: 400;
      height: 40px;
      padding: 10px 20px;
    }
  }
  .loginBox {
    display: flex;
    width: 1000px;
    height: 560px;
    position: relative;
    top: 50%;
    left: 50%;
    margin-left: -500px;
    margin-top: -280px;
    box-shadow: 0px 5px 25px 2px rgba(0, 87, 255, 0.05);
    border-radius: 20px;
   
    .loginBoxLeft {
      width: 439px;
      img {
        width: 100%;
      }
    }
    .loginBoxRight {
      flex-grow: 1;
      background: $bg;
      border-radius: 20px;
    }
    .labelText {
      font-size: 14px;
      font-family: Source Han Sans CN;
      font-weight: 400;
      color: #999;
      line-height: 14px;
    }
  }
   .ver {
      width: 100%;
      text-align: center;
      position: fixed;
      bottom: 20px;
      right:20px;
      font-size: 14px;
      color: #ccc;
      text-align: right;
    }
}
.rememberPass {
  margin-bottom: 26px;
}
// 小屏兼容
@media only screen and (max-width: 1000px) {
  .login-container {
    .loginBox {
      // 登录面板
      width: 100%;
      margin-left: 0;
      left: 0;
      .loginBoxLeft {
        // 登录图片
        display: none;
      }
    }
  }
}
</style>
